section{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #095a55;
}
section::before{
    content: '';
    position: absolute;
    top: 54px;
    left: 0;
    width: 50%;
    height: 100%;
    background: #fff38e;
}
.container{
    position: relative;
    min-width: 1100px;
    min-height: 550px;
    display: flex;
    z-index: 1000;
}
.container .contactInfo{
    position: absolute;
    top: 40px;
    width: 350px;
    height: calc(100% - 80px);
    background: #095a55;
    z-index: 1;
    padding: 40px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0 20px 25px rgba(0,0,0,0.15);
}
.container .contactInfo h2{
    color: #fff;
    font-size: 28px;
    font-weight: 500;
}
.container .contactInfo ul.info{
    position: relative;
    margin: 20px 0;
    padding: 0;
}
.container .contactInfo ul.info li{
    position: relative;
    list-style: none;
    display: flex;
    margin: 20px 0;
    cursor: pointer;
    align-items: flex-start;
}
.container .contactInfo ul.info li span:nth-child(1){
    width: 30px;
    min-width: 30px;
}
.container .contactInfo ul.info li span:nth-child(1) img{
    max-width: 100%;
    filter: invert(1);
}
.container .contactInfo ul.info li span:nth-child(2){
    color: rgb(226, 226, 226);
    margin-left: 10px;
    font-size: 16px;
    font-weight: 300;
}
.container .contactInfo ul.sci{
    position: relative;
    display: flex;
    padding: 0;
}
.container .contactInfo ul.sci li{
    list-style: none;
    margin-right: 15px;
}
.container .contactForm{
    position: absolute;
    padding: 70px 50px;
    padding-left: 250px;
    margin-left: 150px;
    width: calc(100% - 150px);
    height: 100%;
    background: #fff;
}
.container .contactForm h2{
    color: #0f3959;
    font: 24px;
    font-weight: 500;
}
.container .contactForm .formBox{
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-top: 30px;
}
.container .contactForm .formBox .inputBox{
    position: relative;
    margin-bottom: 35px;
}
.container .contactForm .formBox .inputBox.w50{
    width: 47%;
    margin-bottom: 8px;
}
.container .contactForm .formBox .inputBox.w100{
    width: 100%;
    margin-bottom: 8px;
}
.container .contactForm .formBox .inputBox input,
.container .contactForm .formBox .inputBox textarea{
    width: 100%;
    resize: none;
    padding: 5px 0;
    font-size: 18px;
    font-weight: 300;
    color: #333;
    border: none;
    outline: none;
    border-bottom: 1px solid #777;
}
.container .contactForm .formBox .inputBox textarea{
    height: 120px;
}
.container .contactForm .formBox .inputBox input[type="submit"]{
    position: relative;
    cursor: pointer;
    background: #095a55;
    color: #fff;
    border: none;
    max-width: 150px;
    padding: 12px;
}
.container .contactForm .formBox .inputBox input[type="submit"]:hover{
    
    background: #0d948b;
}

@media(max-width : 1200px){
    .container{
        width: 90%;
        min-width: auto;
        margin: 20px;
    }
    .container .contactInfo{
        top:0;
        height: 550px;
        position: relative;
        box-shadow: none;
    }
    .container .contactForm{
        position: relative;
        width: calc(100%-350px);
        margin-left: 0;
        padding-left: 0;
        padding: 40px;
        height: 550px;
    }
}
@media(max-width : 991px){
    section{
        background: #fff38e;
    }
    section::before{
        display: none;
    }
    .container{
        display: flex;
        flex-direction: column-reverse;
    }
    .container .contactInfo{
        width: 100%;
        height: auto;
        flex-direction: row;
    }
    .container .contactInfo ul.sci{
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .container .contactForm{
        width: 100%;
        height: auto;
    }
}
@media(max-width : 600px){
    .container .contactInfo{
        padding: 25px;
        flex-direction: column;
        align-items: flex-starts;
    }
    .container .contactInfo ul.sci{
        margin-top: 40px;
    }
    .container .contactForm .formBox .inputBox.w50{
        width: 100%;
    }
    .container .contactForm{
        padding: 25px;
    }
}